var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('template-slot',{scopedSlots:_vm._u([{key:"leftContainer",fn:function(){return [_c('h3',{staticClass:"cell",staticStyle:{"color":"var(--color-height-text)"}},[_vm._v("端午节介绍")]),_c('el-divider'),_c('img',{staticClass:"content-img",attrs:{"alt":"","src":require("@/assets/images/About/1.webp")}}),_c('p',[_vm._v("端午节，又称端阳节、龙舟节、天中节等，源于自然天象崇拜，由上古时代祭龙演变而来。仲夏端午，苍龙七宿飞升于正南中天，处于全年最“正中”之位，即如《易经·乾卦》第五爻的爻辞曰：“飞龙在天”。")]),_c('p',[_vm._v("其起源涵盖了古老星象文化、人文哲学等方面内容，蕴含着深邃丰厚的文化内涵，在传承发展中杂糅了多种民俗为一体，节俗内容丰富。扒龙舟与食粽是端午节的两大礼俗，这两大礼俗在中国自古传承，至今不辍。")]),_c('p',[_vm._v("端午节，本是上古先民创立用于拜祭龙祖、祈福辟邪的节日。因传说战国时期的楚国诗人屈原在五月五日跳汨罗江自尽，后来人们亦将端午节作为纪念屈原的节日；也有纪念伍子胥、曹娥及介子推等说法。")]),_c('p',[_vm._v("由于屈原深受百姓爱戴，渔民唯恐屈原尸身沉入江底遭食人鱼吞噬，于是拼命用舟桨奋力拍打水面吓走鱼儿。其他村民则用芦叶包裹大米，蒸熟后扔进江里喂食鱼儿，希望因此而保全屈原尸身免受鱼儿侵袭。")]),_c('p',[_vm._v("总的来说，端午节起源于上古先民择“飞龙在天”吉日拜祭龙祖、祈福辟邪，注入夏季时令“祛病防疫\"风尚；把端午视为“恶月恶日”起于北方中原，附会纪念屈原等历史人物纪念内容。")])]},proxy:true},{key:"rightContainer",fn:function(){return [_c('el-table',{staticClass:"mt",staticStyle:{"width":"100%","height":"100%"},attrs:{"data":_vm.tableData,"header-cell-style":_vm.tableHeaderColor,"stripe":""}},[_c('el-table-column',{attrs:{"label":"更多内容","prop":"key"}}),_c('el-table-column',{attrs:{"prop":"value"}})],1),_c('el-collapse',{model:{value:(_vm.activeNames),callback:function ($$v) {_vm.activeNames=$$v},expression:"activeNames"}},[_c('el-collapse-item',{attrs:{"name":"1","title":"您知道吗？"}},[_c('div',[_vm._v("自2008年起，端午节被列为国家法定节假日。2009年9月，联合国教科文组织正式批准将其列入《人类非物质文化遗产代表作名录》，端午节成为中国首个入选世界非遗的节日。")])])],1)]},proxy:true}])})}
